                });
            } /*else if (type === 3) {
                this.$refs.table.exportCsv({
                    filename: '自定义数据',
                    columns: this.columns8.filter((col, index) => index < 4),
                    data: this.data7.filter((data, index) => index < 4)
                });
            }*/
            var that=this;
            var loanTimeStart = "";
            var loanTimeEnd = "";
            var planLtmStart = "";
            var planLtmEnd = "";
            var realTimeStart = "";
            var realTimeEnd = "";
            if(that.formSel.overdue.length != 0){
                if(that.formSel.overdue[0]){
                    loanTimeStart = that.formSel.overdue[0];
                }
                if(that.formSel.overdue[1]){
                    loanTimeEnd = that.formSel.overdue[1];
                }
            }

            if(that.formSel.selectTm.length != 0){
                if(that.formSel.selectTm[0]){
                    planLtmStart = that.formSel.selectTm[0];
                }
                if(that.formSel.selectTm[1]){
                    planLtmEnd = that.formSel.selectTm[1];
                }
            }
            if(that.formSel.repayTm.length != 0){
                if(that.formSel.repayTm[0]){
                    realTimeStart = that.formSel.repayTm[0];
                }
                if(that.formSel.repayTm[1]){
                    realTimeEnd = that.formSel.repayTm[1];
                }
            }

             window.location.href = "/export/repayDetailDown?value=" + that.formSel.input + "&loanTimeStart=" + loanTimeStart + "&loanTimeEnd=" + loanTimeEnd
                                     + "&planLtmStart=" + planLtmStart + "&planLtmEnd=" + planLtmEnd + "&realTimeStart=" + realTimeStart + "&realTimeEnd=" + realTimeEnd;
        },
        changePage(page){
            /*this.data1=this.*/
            this.loadContent(page);
        },
        loadContent(page){
            var firstResult = (parseInt(page) - 1) * 10;
            var maxResults = firstResult + 10;
            var that=this;

          let formData = new FormData();
          formData.append('value', that.formSel.input?that.formSel.input:'');
          formData.append('loanTimeStart', that.formSel.overdue[0]?that.formSel.overdue[0]:'');
          formData.append('loanTimeEnd', that.formSel.overdue[1]?that.formSel.overdue[1]:'');
          formData.append('planLtmStart', that.formSel.selectTm[0]?that.formSel.selectTm[0]:'');
          formData.append('planLtmEnd', that.formSel.selectTm[1]?that.formSel.selectTm[1]:'');
          formData.append('realTimeStart', that.formSel.repayTm[0]?that.formSel.repayTm[0]:'');
          formData.append('realTimeEnd', that.formSel.repayTm[1]?that.formSel.repayTm[1]:'');
          formData.append('firstResult', firstResult);
          formData.append('maxResults', maxResults);

          this.axios
            .post('/detail/repayDetail', formData,
              {headers:{'Content-Type': 'multipart/form-data'}})
            .then(function(respon){
              if(respon.data.resCode == 1){
                that.total = respon.data.count;
                respon.data.result.forEach(function(element) {
                  if(element.clrType==0){
                    element.clrType = "正常还款";
                  }else if(element.clrType==1){
                    element.clrType = "提前还款";
                  }
                }, this);
                that.data1 = respon.data.result;
              }else{
                alert(respon.data.msg);
              }
            })
            .catch(function(resp){
              this.$Message.warning('系统异常');
            });
        },
         reset(){
            this.formSel = {
                    input:'',
                    overdue:'',
                    selectTm:'',
                    repayTm:'',
                }
